.App {
  text-align: center;
  color: #ffffff;
}
.App p{
  text-align: justify;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.modal-content{
  border-radius: 1rem !important;
}
.col-navbar{
  background: #4444bb;
}
.footer{
  background: #eeeeff;
  color: #444499;
}
.photo{
  flex: content;
  width: 100px;
  height: auto;
  border-radius: 50px;
}
.frame{
  align-content: center;
  display: flexbox;
  padding: 2px;
  background: transparent;
  width: 100%;
  border-radius: 50px;
}
.first-col{
  background: #444499 !important; /*#282c34*/
}
.second-col{
  background: #222277 !important;
}
.third-col{
  background: #111155 !important;
}
.personal-info{
  text-align: end;
}
@media screen and (max-width: 576px) {
  .personal-info{
    text-align: center;
  }
}

.custom-circle-button{
  background: #ffffff !important;
}
.custom-circle-button:hover{
  background: #111155 !important;
  color:white !important;
}
.custom-circle-button:focus{
  background: #222255 !important;
  color:white !important;
}
.custom-button{
  background: #ffffff !important;
  border-radius: 50px !important;
}
.custom-button:hover{
  background: #111155 !important;
  color:white !important;
}
.custom-button:focus{
  background: #222255 !important;
  color:white !important;
}